.form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  border: 3px solid lightblue;
  padding: 20px;
  width: 300px;
  background-color: lightblue;
}

.label {
  display: flex;
  flex-direction: column;
  font-size: 20px;
  gap: 5px;
  align-items: center;
}

.input {
  font-size: 14px;
  padding: 5px;
  outline-color: rgb(43, 43, 172);
  width: 150px;
}

.form-btn {
  margin-right: auto;
  margin-left: auto;
  padding: 10px 15px;
  font-size: 16px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  background-color: rgba(131, 131, 221, 0.282);
  margin-top: 8px;
}

.form-btn:hover,
.form-btn:focus {
  background-color: rgba(0, 0, 255, 0.282);
  transition: background-color cubic-bezier(0.215, 0.61, 0.355, 1);
}
.btn {
  margin-right: auto;
  margin-left: auto;
  padding: 10px 15px;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: lightblue;
  margin-top: 8px;
}
.btn:hover {
  background-color: rgba(165, 42, 42, 0.399);
}
