.list {
  display: flex;
  flex-direction: column;
  gap: 15px;
  font-size: 18px;
  font-weight: 500;
  width: 400px;
  padding-left: 20px;
}
.wrap {
  display: inline-flex;
  gap: 10px;
}

.contact {
  width: 500px;
}

.list-btn {
  margin: auto;
  padding: 7px 12px;
  min-width: 50px;
  font-size: 16px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  background-color: rgba(255, 0, 0, 0.208);
  transition: background-color cubic-bezier(0.215, 0.61, 0.355, 1);
}
.list-btn:hover,
.list-btn:focus {
  background-color: rgba(255, 29, 8, 0.596);
  transition: background-color cubic-bezier(0.215, 0.61, 0.355, 1);
}
